.zoom-button-container {
   display: flex;
   height: 30px;
   align-items: center;
   border-radius: 10px;
   background-color: #586af060;
}

.zoom-button {
   background-color: #5869F0;
   border: none;
   width: 30px;
   height: 100%;
   cursor: pointer;
   border-radius: 10px;
   color: #fff;
   font-size: 22px;
}

.zoom-button:hover {
   background-color: #4659f0;
}

.zoom-text {
   width: 60px;
   text-align: center;
   font-weight: bold;
}

.light .zoom-text {
   color: #000;
}

.dark .zoom-text {
   color: #fff;
}

@media only screen and (max-width: 1300px) {
   .zoom-text {
      width: 45px;
      font-size: 14px;;
   }
   .zoom-button {
      width: 30px;
   }
}