.light {
   fill: #000;
}

.dark {
   fill: #fff;
}

.cross {
   height: 30px;
   width: 3px;
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%) rotate(45deg);

}

.cross.light {
   background-color: #000;
}

.cross.dark {
   background-color: #fff;
}
